.Input{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.Label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement{
    outline: none;
    border: 1px solid #ccc;
    padding: 6px 10px;
    background-color: white;
    font: inherit;
    display: block;
    width: 100%;
    box-sizing: border-box;
}

.InputElement :focus{
    outline: none;
    background-color: #ccc;
}

.Invalid{
    border: 1px solid red;
    background-color: rgb(231, 103, 88);
}

.ValidationError {
    color: red;
    margin: 5px 0;
} 